import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { Router } from '@angular/router';
@Component({
    selector: 'child-cell',
    template: `
        <div class="dealer-status">
       <!-- <i  [ngClass]="{'icon-Complete-Done': params && params.data  && params.data.activeStatus == A}" class="icon-Complete-Default"></i>
        <i [ngClass]="{'icon-In-progress-Done': params && params.data && params.data.activeStatus == I}" class="icon-In-Progress-Default"></i>
        -->
    </div>
    `

})


export class AgActiveStatusActionsRenderer implements ICellRendererAngularComp {
    public params: any;
    constructor( private router: Router ) {}
    agInit(params: any): void {
        this.params = params;
    }
    refresh(): boolean {
        return false;
    }
}
