import { Component, OnInit, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { MockData } from '../../../constants/mock-data';
import { TicketService } from '../ticket.service';
import { Ticket } from '../ticket';
import { Province } from '../../../models/province';
import { first } from 'rxjs/operators';
import { Router } from '@angular/router';
import { TicketStatus } from '../../../models/ticketstatus';
import { TSO } from '../../../models/tso';
import { cellPhoneFormatter } from '../../../_helpers/cell-phone-fomatter';
import { DataSharingService } from 'src/services/data-sharing.service';
import { AppPersistentUtilityService, Persistent } from 'src/services/app-persistent-utility.service';
@Component({
    selector: 'app-list-ticket',
    templateUrl: './list-ticket.component.html',
    styleUrls: ['./list-ticket.component.css']
})
export class ListTicketComponent implements OnInit, OnDestroy {
    workFlowViewcolumnDefs = [];
    workFlowViewrowData = [];
    // added by Husnain
    // to pass whether to runprocessCellCallback or not 
    public runProcessCellCallback: boolean = true;
    tickets: any[];
    ticketstatus: TicketStatus[];
    selectedValue;
    searchvalue;
    selectedTSO: string = "0";
    tso: TSO[];
    public breadcrumbUrl;
    categories: any[] = [{ label: 'Ticket ID', value: 'ticketId' }, { label: 'Farmer CNIC', value: 'cnic' }, { label: 'Farmer Cell Phone', value: 'Phone' }];
    provinces: Province[];
    private searchedValue: string = "";
    public tsoId: number = 0;
    public tsoName: string;
    private params: string;
    public currentPage;
    currentpage;

    private searchedcat: string;
    private pages: number = 1;
    public recordsPerPage: number = 15;
    public totalrecords: number = 0;
    totalrec: number;
    constructor(readonly cdr: ChangeDetectorRef, private _appPersistenceUtility: AppPersistentUtilityService, public ticketsService: TicketService, private router: Router, private cellPhoneFormatter: cellPhoneFormatter,
        private dataSharingService: DataSharingService) {
    }
    ngOnDestroy(): void {
        this._appPersistenceUtility.ticketsPersistence.next(new Persistent((this.breadcrumbUrl == "" ? 0 : this.breadcrumbUrl), this.currentPage, this.tsoId, this.tsoName, this.totalrecords, this.tickets, true))
    }

    setPage(): void {
        this._appPersistenceUtility.ticketsPersistence.subscribe(res => {
            if (res.currentPageNumber !== 0 && res.data !== null) {
                this.breadcrumbUrl = res.appTab;
                this.currentPage = res.currentPageNumber;
                if (res.currentPageNumber == 1)
                    this.currentPage = 0;
                this.tsoId = res.tsoId;
                this.tickets = res.data;
                this.totalrecords = res.totalPages;
            } else {
                this.currentPage = 0;
            }
        });
    }

    ngOnInit() {
        this.setPage()
        if (this.tsoId != 0) {
            this.params = 'pageNumber=' + (this.currentPage == 0 ? 0 : this.currentPage - 1) + '&pageSize=' + this.recordsPerPage + '&tsoID=' + this.tsoId;
            this.selectedTSO = this.tsoId.toString();
            this.searchedcat = "tso";
            this.searchedValue = this.tsoId.toString();
        }
        else {
            if ((this.breadcrumbUrl == 0 || this.breadcrumbUrl == undefined) && (this.tsoId == 0)) {
                this.params = 'pageNumber=' + (this.currentPage == 0 ? 0 : this.currentPage - 1) + '&pageSize=' + this.recordsPerPage;
            }
            else {
                this.searchedcat = "status"; this.searchedValue = this.breadcrumbUrl;
                this.params = 'pageNumber=' + (this.currentPage == 0 ? 0 : this.currentPage - 1) + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            }
        }



        // set active tab status
        // accordingly breadcrubmurl will be active
        this.dataSharingService.activeBreadCrumburl("tickets");
        this.selectedValue = 'ticketId';
        //this.selectedValue = 'Select Category';

        if (this.tsoId != 0) {
            this.ticketsService.getTicketsById(this.params).pipe(first()).subscribe(data => {
                this.tickets = data['data'];
                this.totalrecords = data['totalCount'];
                this.totalrec = data['totalCount'];
                this.getWfGridData();
            });
        }
        else {
            this.ticketsService.getTickets(this.params).pipe(first()).subscribe(data => {
                this.tickets = data['data'];
                // this will keeps on updating and will be used in pagination of ag-grid
                // based on search critera
                this.totalrecords = data['totalCount'];

                // this will be fixed and will be used in statuses count (breadcrumburl)
                this.totalrec = data['totalCount'];
                this.getWfGridData();
            });
        }

        this.ticketsService.getTicketsStatus().pipe(first()).subscribe(data => {
            // onNgInit getTicketStatus returns wrong count for Open tickets (server side issue)
            this.ticketstatus = data;
            console.log("ticketstatus", this.ticketstatus);
        });
        this.ticketsService.getTSO().pipe(first()).subscribe(data => {
            console.log("TSO Data", data)
            this.tso = data;
        });

        // this.setPage()
    }


    delete(ticket: Ticket): void {
        // this.tickets = this.tickets.filter(h => h !== ticket);
        // this.ticketsService.deleteTicket(4).subscribe();
    }
    addedit(id) {
        // this.router.navigate(['/assign-ticket']);
        this.router.navigate(['/create-tickets']);

    }

    pageChanged(event: any) {
        //this.toastr.success('Hello world!', 'Page Changed');
        this.pages = event.page;
        this.pages = this.pages - 1;

        if (this.searchedValue == "") {
            console.log("sdbkvalue", this.searchedValue)
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage;
        }
        else if (this.searchedcat == "tso") {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage + '&tsoID=' + this.searchedValue;
        }
        else {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        }
        if (this.searchedcat != "tso") {

            this.ticketsService.getTickets(this.params).pipe(first()).subscribe(data => {
                this.tickets = data['data'];
                this.getWfGridData();
            });
        }
        else {
            console.log("PARAMAAAS", this.params)
            this.ticketsService.getTicketsById(this.params).pipe(first()).subscribe(data => {
                console.log("sakjhvb", data);
                this.tickets = data['data'];
                this.getWfGridData();

            });
        }
    }

    search(searchcat: string, searchval: string) {
        this.currentPage = 0;
        console.log("searchcat", searchcat);
        console.log("searchval", searchval);
        console.log("breadcrumbUrl: ", this.breadcrumbUrl);

        this.searchedcat = searchcat;
        this.searchedValue = searchval;

        console.log(this.searchedValue);

        // Select TSO is selected (No TSO is selected)
        // Consider it as if All breadcrumurl is clicked
        if (searchcat == "tso" && searchval == "0") {
            console.log("0")
            searchcat = "status";
            searchval = "";
            this.searchedcat = searchcat;
            this.searchedValue = searchval;
            this.tsoId = Number.parseInt(this.searchedValue);
        }
        if (searchcat == "status") {
            console.log("1")
            this.searchvalue = "";
        }
        if (this.searchedValue == "") {
            console.log("2")
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage;
            this.selectedTSO = "0"; // should be set to Select to TSO if user changes search critera
            this.breadcrumbUrl = 0;
            this._appPersistenceUtility.ticketsPersistence.next(new Persistent(this.breadcrumbUrl, this.currentPage, this.tsoId, this.tsoName, this.totalrecords, this.tickets, true));
        }
        else {
            if (searchcat == "tso") {
                console.log("3")
                // this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&tsoID=' + this.searchedValue;
                this.breadcrumbUrl = ""; // dont active any breadcrumburl
                this.searchvalue = ""; // clear search section
                this.searchedValue = searchval;
                this.tsoId = Number.parseInt(this.searchedValue);
                this.tsoName = this.tso.find(id => id.tsoid == searchval).tsoname;
                this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&tsoID=' + this.tsoId;
                this._appPersistenceUtility.ticketsPersistence.next(new Persistent(0, this.currentPage, this.tsoId, this.tsoName, this.totalrecords, this.tickets, true));
            }
            else {
                if (searchcat == 'Phone') {
                    this.searchedValue = this.cellPhoneFormatter.reformatPhoneNoServer(this.searchedValue);
                }
                console.log("4")
                this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
                this.selectedTSO = "0"; // should be set to Select to TSO if user changes search critera
                this.breadcrumbUrl = searchval;
                this._appPersistenceUtility.ticketsPersistence.next(new Persistent(this.breadcrumbUrl, this.currentPage, this.tsoId, this.tsoName, this.totalrecords, this.tickets, true));
            }

        }
        if (searchcat == "tso") {
            console.log("5")
            console.log("PARAMAAAS", this.params)
            this.ticketsService.getTicketsById(this.params).pipe(first()).subscribe(data => {
                console.log("sakjhvb", data);
                this.tickets = data['data'];
                this.getWfGridData();
            });
        }
        else {
            console.log("6")
            this.ticketsService.getTickets(this.params).pipe(first()).subscribe(data => {
                this.tickets = data['data'];
                this.totalrecords = data['totalCount'];
                console.log("Total records: ", this.totalrecords);
                console.log("Tickets: ", this.tickets);
                this.getWfGridData();
            });
        }

        this.currentPage = 1;
    }

    getWfGridData() {
        const colDef: any[] = MockData.agGridColumnDefs_Tickets;
        this.workFlowViewcolumnDefs = colDef;
        this.workFlowViewrowData = this.tickets;
    }
    ResetVal(val) {
        // commented by Husnain
        // if (val == 'Phone') {
        //     this.searchvalue = "92";
        // }
        // else {
        //     this.searchvalue = "";
        // }
        console.log("reset");
        // added by Husnain
        this.searchedValue = "";
    }
    onGridReady(params) {
        params.api.sizeColumnsToFit();
    }
}
