import { Component, OnInit } from '@angular/core';
import { MockData } from '../../../constants/mock-data';
import { DealerService } from '../dealer.service';
import { Dealer } from '../dealer';
import { Province } from '../../../models/province';
import { first } from 'rxjs/operators';
import { Router } from '@angular/router';
import { Region } from 'src/models/region';
import { District } from 'src/models/district';
import { Salespoint } from '../../../models/salespoint';
import { TSO } from '../../../models/tso';
import { DataSharingService } from 'src/services/data-sharing.service';
import { cellPhoneFormatter } from '../../../_helpers/cell-phone-fomatter';

@Component({
    selector: 'app-list-dealer',
    templateUrl: './list-dealer.component.html',
    styleUrls: ['./list-dealer.component.css']
})
export class ListDealerComponent implements OnInit {
    workFlowViewcolumnDefs = [];
    workFlowViewrowData = [];
    dealers: Dealer[];
    selectedValue;
    selectedRegion: string = "0";
    selectedDistrict: string = "0";
    selectedTSO: string = "0";
    selectedSalespoint: string = "0";
    searchvalue;
    public disval;
    public breadcrumbUrl;
    allFlag;

    provinceFlag;
    categ = ['Region'];
    categories = ['Phone', 'CNIC'];
    provinces: Province[];
    regions: Region[];
    salespoints: Salespoint[];
    tso: TSO[];
    districts: District[];
    private searchedValue: string = "";
    private params: string;
    private currentPage: number = 0;
    currentpage;
    private searchedcat: string;
    private pages: number = 1;
    public recordsPerPage: number = 15;
    public totalrecords: number = 0;
    showBoundaryLinks = true;
    public runProcessCellCallback: boolean = true;



    constructor(public dealersService: DealerService, private router: Router, private dataSharingService: DataSharingService,
        private cellPhoneFormatter: cellPhoneFormatter) {

    }
    ngOnInit() {
        // set active tab status
        // accordingly breadcrubmurl will be active
        this.dataSharingService.activeBreadCrumburl("dealers");

        this.selectedValue = 'CNIC';

        this.dealersService.getDealers('pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage).pipe(first()).subscribe(data => {
            this.dealers = data['data'];
            this.totalrecords = data['totalCount'];
            // this.reformatDealer();
            this.getWfGridData();
        });
        this.dealersService.getProvinces().pipe(first()).subscribe(data => {
            this.provinces = data;
            // console.log("provinces: ", this.provinces)       
        });
        // this.dealersService.getSalePoints().pipe(first()).subscribe(data => {
        //     // console.log("Salespoint Data", data)
        //     this.salespoints = data;
        // });
        this.dealersService.getTSO().pipe(first()).subscribe(data => {
            // console.log("TSO Data", data)
            this.tso = data;
        });
        this.dealersService.getRegion().pipe(first()).subscribe(data => {
            this.regions = data;

        });
        // this.dealersService.getDistrict().pipe(first()).subscribe(data => {
        //     this.districts = data;

        // });

    }
    addedit(id) {
        this.router.navigate(['/dealers/' + id]);
    }
    pageChanged(event: any) {
        this.pages = event.page;
        this.pages = this.pages - 1;
        if (this.searchedValue == "") {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage;
        }
        else {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        }
        this.dealersService.getDealers(this.params).pipe(first()).subscribe(data => {
            this.dealers = data['data'];
            // this.reformatDealer();
            this.getWfGridData();
        });

    }
    search(searchcat: string, searchval: string) {
        this.searchedcat = searchcat;
        this.searchedValue = searchval;

        console.log("searchvalue:", this.searchedValue);
        console.log("searchcat:", this.searchedcat)

        if (searchcat == "Province") {
            this.searchvalue = "";
            this.selectedRegion = "0";
            this.selectedDistrict = "0";
            this.selectedSalespoint = "0";
            this.selectedTSO = "0";
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            this.allFlag = 0;
            this.provinceFlag = 1;
            this.breadcrumbUrl = searchval; // active respective to searched province
            this.districts = [];
            this.salespoints = [];
        }
        // if (searchcat == "Region") {
        //     this.searchvalue = "";
        //     this.selectedDistrict = "0";
        //     this.selectedSalespoint = "0";
        //     this.selectedTSO = "0";
        //     this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        //     this.allFlag = 0;
        //     this.provinceFlag = 1;
        //     // this.breadcrumbUrl = 'All';
        //     this.breadcrumbUrl = ''; // no need to active breadcrumb url when search by region
        // }

        // if (searchcat == "District") {
        //     this.searchvalue = "";
        //     this.selectedRegion = "0";
        //     this.selectedSalespoint = "0";
        //     this.selectedTSO = "0";
        //     this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        //     this.allFlag = 0;
        //     this.provinceFlag = 1;
        //     // this.breadcrumbUrl = searchval;
        //     this.breadcrumbUrl = ''; // no need to active breadcrumb url when search by district
        // }

        // added by Husnain
        if (searchcat == "Region") {
            this.getDistricts(parseInt(searchval));
            return;
        }
        if (searchcat == "District") {
            this.getSalesPoints(parseInt(searchval));
            return;
        }

        if (searchcat == "salepoint") {
            this.searchvalue = "";
            // this.selectedRegion = "0";
            // this.selectedDistrict = "0";
            this.selectedTSO = "0";
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            this.allFlag = 0;
            this.provinceFlag = 1;
            // this.breadcrumbUrl = 'All';
            this.breadcrumbUrl = ''; // no need to active breadcrmb url when search by salepoint
        }
        if (searchcat == "tso") {
            this.searchvalue = "";
            this.selectedRegion = "0";
            this.selectedDistrict = "0";
            this.selectedSalespoint = "0";
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            this.allFlag = 0;
            this.provinceFlag = 1;
            // this.breadcrumbUrl = 'All';
            this.breadcrumbUrl = ''; // no need to active breadcrmb url when search by tso
            this.districts = [];
            this.salespoints = [];
        }
        // Added by Husnain to handle CNIC and Phone search
        if (searchcat == 'CNIC' || searchcat == 'Phone') {
            // reformat phone no before sending to the server
            // if(searchcat == 'Phone'){
            //     this.searchedValue = this.cellPhoneFormatter.reformatPhoneNoServer(this.searchedValue);
            // }
            this.selectedRegion = "0";
            this.selectedDistrict = "0";
            this.selectedSalespoint = "0";
            this.selectedTSO = "0";
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            this.allFlag = 0;
            this.provinceFlag = 1;
            this.breadcrumbUrl = ''; // no need to active breadcrmb url when search by CNIC or Phone
            this.districts = [];
            this.salespoints = [];
        }
        if (this.searchedValue == "") {
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage;
            this.allFlag = 1;
            this.provinceFlag = 0;
            this.breadcrumbUrl = 'All'; // need to active all breadcrumbrul when searchBox is empty
            this.districts = [];
            this.salespoints = [];
        }

        // console.log("bread crumb url", this.breadcrumbUrl);
        // console.log("parameters: ", this.params);

        this.dealersService.getDealers(this.params).pipe(first()).subscribe(data => {
            this.dealers = data['data'];
            // console.log("data filter", data)
            this.totalrecords = data['totalCount'];
            // this.reformatDealer();
            // console.log("Total Records:",this.totalrecords);
            console.log("dealer: ", this.dealers);
            console.log("total count: ", this.totalrecords);
            this.getWfGridData();
        });
        this.currentpage = 1;
    }
    getWfGridData() {
        const colDef: any[] = MockData.agGridColumnDefs_Dealers;
        // console.log("Column Def", MockData.agGridColumnDefs_Dealers)
        this.workFlowViewcolumnDefs = colDef;
        this.workFlowViewrowData = this.dealers;
        // console.log(this.dealers);
        // console.log("ag grid");
        // console.log("isse" + colDef);
    }

    onGridReady(params) {
        params.api.sizeColumnsToFit();
    }

    getDistricts(id) {
        this.searchvalue = "";
        this.selectedDistrict = "0";
        this.selectedSalespoint = "0";
        this.selectedTSO = "0";
        this.allFlag = 0;
        this.provinceFlag = 1;
        this.breadcrumbUrl = ''; // no need to active breadcrumb url when search by region

        // set districts and salespoints array to empty
        this.districts = [];
        this.salespoints = [];

        // fetch all regions respective to district
        this.dealersService.getDistricts(id).pipe(first()).subscribe(data => {
            console.log("Districts: ", data);
            this.districts = data;
            this.dealers = [];
            this.totalrecords = 0;
            this.getWfGridData();
        });
    }

    getSalesPoints(id) {
        this.searchvalue = "";
        this.selectedSalespoint = "0";
        this.selectedTSO = "0";
        this.params = this.searchvalue;
        this.allFlag = 0;
        this.provinceFlag = 1;
        this.breadcrumbUrl = ''; // no need to active breadcrumb url when search by district

        // set salespoints array to empty
        this.salespoints = [];

        // Uncomment it when it will be implemented
        this.dealersService.getSalePoint(parseInt(this.searchedValue)).pipe(first()).subscribe(data => {
            console.log("sales points by district: ", data);
            this.salespoints = data;
            this.dealers = [];
            this.totalrecords = 0;
            this.getWfGridData();
        });
    }

    reformatDealer() {
        // all farmers
        for (const [index, value] of Object.entries(this.dealers)) {
            let farmer = this.dealers[index];
            // individual farmer
            for (let key in farmer) {
                farmer[key] = farmer[key] && (key == "cellPhone")
                    ? this.cellPhoneFormatter.reformatPhoneNoClient(farmer[key]) : farmer[key];
            }
        }
    }

}
