import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataSharingService {

  private breadcrumbUrlSource = new BehaviorSubject("");
  breadcrumbUrlStatus = this.breadcrumbUrlSource.asObservable();

  constructor() { }
  
  activeBreadCrumburl(activeTab: string) {
    this.breadcrumbUrlSource.next(activeTab);
  }
}
