import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { AuthService } from '../services/auth.service';
import { ToastrService } from 'ngx-toastr';

@Injectable()
export class ErrorInterceptor implements HttpInterceptor {
    private errorCount;
    constructor(private authenticationService: AuthService, private ts: ToastrService) {
        this.errorCount = 0;
    }
    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(request).pipe(catchError(err => {
            if (err.status === 401) {

                // to track failed responses from server
                this.errorCount = this.errorCount + 1;

                // auto logout if 401 response returned from api
                this.authenticationService.logout();

                // show alert only for once
                // since multiple requests responses are in pending state
                // all those reponses needs to be fullfilled before the reload takes place
                // intercept intercepts each request response
                // to prevent it, we will alert for once only and not for multiple times
                this.errorCount == 1 ? alert("Session Expired.") : '';
                location.reload(true);
                this.errorCount = this.errorCount + 1;

                // prevent catch to throw error on 401 response since the user is going to be logged out
                // since multiple request were made and response in pending mode
                // accordingly no toasters will appear saying about suucess or fail
                return;
            }
            const error = err.error.message || err.statusText;
            this.ts.error("Error: " + error);
            return throwError(error);
        }));
    }
}