import { Injectable } from "@angular/core";

@Injectable({
    providedIn: 'root'
})
export class cellPhoneFormatter {

    constructor() {
    }

    public reformatPhoneNoClient(phoneNo: string) {
        
        // server sends 92 in case of empty phone no
        if (phoneNo == "92") {
            return " ";
        }

        // truncate - if it exists since we want to place it after 4 digits
        phoneNo = phoneNo.replace('-', ''); 
        
        // replace Pak Country 92 code with 0
        phoneNo.substring(0,2) == "92"? phoneNo = '0' + phoneNo.slice(2) : '';
     
        // place - sign after 4 digits
        let arrGivenString = phoneNo.split(""); // convert it into array of characters
        arrGivenString.splice(4, 0, '-'); // place - after 4th digit
        phoneNo = arrGivenString.join(''); // join all characters into one string
      
        return phoneNo;
    }

    public reformatPhoneNoServer(phoneNo: string) {

        let regex = /\d/; // any digit
        phoneNo = phoneNo.replace("-", ""); // omit -
        phoneNo = phoneNo.replace(regex, "92"); // replace first digit (can be any digit) with 92
        
        return phoneNo;
    }
}