import { Component, OnInit } from '@angular/core';
import { TicketType } from '../../../models/ticketTypes';
import { Ticket } from '../ticket';
import { Farms } from '../../farmer/farms';
import { Farmers } from '../../farmer/farmers';

import { TicketService } from '../ticket.service';
import { Router, ActivatedRoute } from '@angular/router';
import { first } from 'rxjs/operators';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Location } from '@angular/common';
import { DataSharingService } from 'src/services/data-sharing.service';
import { cellPhoneFormatter } from '../../../_helpers/cell-phone-fomatter';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-create-tickets',
  templateUrl: './create-tickets.component.html',
  styleUrls: ['./create-tickets.component.css']
})
export class CreateTicketsComponent implements OnInit {
  public ticketTypes: TicketType[];
  form: FormGroup;
  myDate = new Date();
  ticket: Ticket = new Ticket();
  farms: Farms[];
  farmer: Farmers;
  CreatedBy;
  public ticketValue;
  public visitDate;
  public cnic;
  public phone;
  data;
  public description;

  ticketInformation: boolean = false;
  farmerInfoProfile: boolean = false;
  public isChecked: boolean = false;
  constructor(private location: Location, private route: ActivatedRoute, fb: FormBuilder, private ticketsService: TicketService,
    private router: Router, private dataSharingService: DataSharingService, private cellPhoneFormatter: cellPhoneFormatter, private ts: ToastrService) {


    // this.form = fb.group({
    //   "dealerName": ["", ],
    //   "cnic": ["", ],
    //   "ntn": ["", ],
    //   "strn": ["", ],
    //   "dealerCategory": ["", ],
    //   "dealerStatus": ["", ],
    //   "taxProfileDate": ["", ],
    //   "currentStatus": ["",],
    //   "ccnumber": ["", ],
    //   "sherpCc": ["", ],
    //   "proprieter": ["", ],
    //   "address": ["", ],
    //   "province": ["", ],
    //   "districtCode": ["", ],
    //   "cityVillage": ["", ],
    //   "tehsilCodeNavigation": ["", ],
    //   "saleRegion": ["", ],
    //   "salePoint": ["", ],
    //   "latitude": ["", ],
    //   "longitude": ["", ],
    //   "cellPhone": ["", ],
    //   "landline": ["", ],
    //   "email": ["", ]
    // })

  }

  ngOnInit() {
    // set active tab status
    // accordingly breadcrubmurl will be active
    this.dataSharingService.activeBreadCrumburl("tickets");

    this.ticketsService.getTicketType().pipe(first()).subscribe(data => {
      this.ticketTypes = data;
      console.log("ticket type: ", this.ticketTypes);
      this.ticketsService.getUserProfile().subscribe(x => {

        this.data = x.data;
        this.CreatedBy = this.data.callCenterAgentId

      })

    });

  }

  onChange() {
    if (this.ticketValue != null && (this.visitDate != null || this.visitDate != undefined)) {
      this.ticketInformation = true;
    }
  }

  save(): void {
    if (!(this.farms && this.farms.length)) {
      this.ts.show("Farmer donot have any farm(s).", '', { timeOut: 10000 });
    }
    if (!(this.ticket && this.ticket.ticketTypeId && this.ticket.visitDate && this.ticket.farmerId && this.isChecked)) {
      this.ts.show("Must select a farm", '', { timeOut: 10000 });
    }
    else {
      let TicketObj = this.ticket;
      delete TicketObj.farmer;
      TicketObj.createdBy = this.CreatedBy;
      this.ticketsService.addTicket(TicketObj)
        .subscribe(ticketD => {
          console.log("succ", ticketD)
          if (ticketD != undefined) {
            ticketD && this.ts.success("Ticket Locked!");
            ticketD && this.router.navigate(['assign-ticket/' + ticketD.ticketId])
          }
          else {
            this.ts.error("There is some problem, please try again later.");
          }
          // this.ts.success("Operation Performed Successfully");
        });
      // this.location.back();
    }

  }


  getFarmerDetail(key?: string, value?: string) {
    if (key == "phone") {
      value = this.cellPhoneFormatter.reformatPhoneNoServer(value);
    }
    if (key && value) {

      let params = '?' + key + '=' + value;

      console.log(params)
      // phone=923111111111

      this.ticketsService.getExactFarmer(params).pipe(first()).subscribe(data => {
        if (data && data[0]) {
          this.farmer = data[0];
          console.log("farmer", this.farmer)
          this.ticket.farmerId = data[0].farmerId;
          // added by Husnain
          // to reformat necessary fields before being shown in form
          this.reformatTicket();

          this.ticketsService.getfarmerFarms(this.ticket.farmerId).pipe(first()).subscribe(farmsArray => {
            this.farms = farmsArray;
            console.log("farms", this.farms)
          })
        }
        else {
          this.farmer = null;
          this.ticket.farmId = null;
          this.ticket.farmer = null;
          this.farms = [];
          this.isChecked = false;
        }
        console.log("t", this.ticket)
      })
    }

  }

  farmSelected(id) {
    this.ticket.farmId = id;
    console.log(this.ticket);
    this.isChecked = true;
  }

  farmersInfoProfile() {
    if (this.cnic != null && this.phone != null && this.description != null) {
      this.farmerInfoProfile = true;
    }
  }
  goBack() {
    this.location.back();
  }

  reformatTicket() {
    // ticket reformat
    let farmer = this.farmer;
    for (let key in farmer) {
      farmer[key] = (key == "cellPhone") && farmer[key]
        ? this.cellPhoneFormatter.reformatPhoneNoClient(farmer[key]) : farmer[key];
    }

  }

}
