import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { MockData } from '../../../constants/mock-data';
import { Router } from '@angular/router';
import { FarmersService } from '../farmers.service';
import { first } from 'rxjs/operators';
import { Province } from '../../../models/province';
import { Farmers } from '../farmers';
import { cellPhoneFormatter } from '../../../_helpers/cell-phone-fomatter';
import { DataSharingService } from 'src/services/data-sharing.service';
import { AppPersistentUtilityService, FarmerPersistent } from 'src/services/app-persistent-utility.service';
@Component({
    selector: 'app-farmers-detail',
    templateUrl: './farmers-detail.component.html',
    styleUrls: ['./farmers-detail.component.css']
})
export class FarmersDetailComponent implements OnInit, OnDestroy {
    farmersViewcolumnDefs = [];
    farmersViewrowData = [];
    // added by Husnain
    // to pass whether to runprocessCellCallback or not 
    public runProcessCellCallback: boolean = true;
    categories = ['Phone', 'CNIC',];
    private pages: number = 1;
    private params: string;
    public recordsPerPage: number = 15;
    public totalrecords: number = 0;
    showBoundaryLinks = true;
    private currentPage: number = 0;
    public breadcrumbUrl;
    searchvalue;
    selectedValue;
    currentpage;
    provinces: Province[];
    private searchedcat: string;
    private searchedValue: string = "";
    farmers: Farmers[];
    constructor(private router: Router, private _appPersistenceUtility: AppPersistentUtilityService, public farmersService: FarmersService, private cellPhoneFormatter: cellPhoneFormatter,
        private dataSharingService: DataSharingService) {
    }

    ngOnDestroy(): void {
        this._appPersistenceUtility.farmerPersistence
            .next(
                new FarmerPersistent(
                    (this.breadcrumbUrl == "" ? 0 : this.breadcrumbUrl),
                    this.currentpage,
                    this.totalrecords,
                    this.searchedcat, this.searchedValue, this.farmers, true)
            );
    }

    setPage(): void {
        this._appPersistenceUtility.farmerPersistence.subscribe(res => {
            if (res.currentPageNumber !== 0 && res.data !== null) {
                this.breadcrumbUrl = res.appTab;
                this.currentpage = res.currentPageNumber;
                this.searchedcat = res.cat;
                this.searchedValue = res.val;
                this.farmers = res.data;
                this.totalrecords = res.totalPages;
            } else {
                this.currentpage = 1;
            }
        });
    }

    ngOnInit() {
        this.setPage();
        if (this.breadcrumbUrl == 0 || this.breadcrumbUrl == undefined) {
            this.params = 'pageNumber=' + (this.currentpage - 1) + '&pageSize=' + this.recordsPerPage;
        }
        else {
            this.params = 'pageNumber=' + (this.currentpage - 1) + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        }

        // set active tab status
        // accordingly breadcrubmurl will be active
        this.dataSharingService.activeBreadCrumburl("farmer");

        this.selectedValue = 'CNIC';
        this.farmersService.getFarmers(this.params).pipe(first()).subscribe(data => {
            this.farmers = data['data'];
            this.totalrecords = data['totalCount'];
            console.log("Farmers", this.totalrecords);
            console.log("Farmers", this.farmers);
            this.reformatFarmer();
            this.getWfGridData();
        });
        console.log("called")
        this.farmersService.getProvinces().pipe(first()).subscribe(data => {
            this.provinces = data;
        });
    }

    getWfGridData() {
        const colDef: any[] = MockData.agGridColumnDefs_Farmers;

        colDef.map(column => {
            column.headerName = column.headerName;
        });

        this.farmersViewcolumnDefs = colDef;
        this.farmersViewrowData = this.farmers;

        console.log("farmersViewcolumnDefs", this.farmersViewcolumnDefs)
        console.log("farmersViewrowData", this.farmersViewrowData)
    }

    addFarmer(id) {
        this.router.navigate(['/add-farmer/' + id]);
    }
    pageChanged(event: any) {
        //this.toastr.success('Hello world!', 'Page Changed');
        this.pages = event.page;
        this.pages = this.pages - 1;
        if (this.searchedValue == "") {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage;
        }
        else {
            this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
        }
        this.farmersService.getFarmers(this.params).pipe(first()).subscribe(data => {
            this.farmers = data['data'];
            console.log(this.farmers);
            this.reformatFarmer()
            this.getWfGridData();
        });
    }

    search(searchcat: string, searchval: string) {
        console.log(searchcat);
        console.log(searchval);
        this.searchedcat = searchcat;
        this.searchedValue = searchval;
        console.log(this.searchedValue);
        if (searchcat == "Province") {
            this.searchvalue = "";
        }
        if (this.searchedValue == "") {
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage;
            this.breadcrumbUrl = 0;
            this._appPersistenceUtility.farmerPersistence
                .next(new FarmerPersistent(0, this.currentpage, this.totalrecords, null, null, this.farmers, true));
        }
        else {
            if (searchcat == 'Phone') {
                this.searchedValue = this.cellPhoneFormatter.reformatPhoneNoServer(this.searchedValue);
            }
            this.params = 'pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage + '&filter=' + this.searchedcat + '&value=' + this.searchedValue;
            this.breadcrumbUrl = searchval;
            this._appPersistenceUtility.farmerPersistence
                .next(new FarmerPersistent(this.breadcrumbUrl, this.currentpage, this.totalrecords, this.searchedcat, this.searchedValue, this.farmers, true));
        }
        this.farmersService.getFarmers(this.params).pipe(first()).subscribe(data => {
            this.farmers = data['data'];
            this.totalrecords = data['totalCount'];
            console.log(this.totalrecords);
            this.reformatFarmer()
            this.getWfGridData();
        });
        this.currentpage = 1;
    }
    ResetVal(val) {
        // commented by Husnain
        // if (val == 'Phone') {
        //     this.searchvalue = "92";
        // }
        // else {
        //     this.searchvalue = "";
        // }

        // added by Husnain
        this.searchedValue = "";
    }
    onGridReady(params) {
        params.api.sizeColumnsToFit();
    }

    reformatFarmer() {
        // all farmers
        for (const [index, value] of Object.entries(this.farmers)) {
            let farmer = this.farmers[index];
            // individual farmer
            for (let key in farmer) {
                farmer[key] = farmer[key] && (key == "cellPhone")
                    ? this.cellPhoneFormatter.reformatPhoneNoClient(farmer[key]) : farmer[key];
            }
        }
    }
}