import { AppPersistentUtilityService, Persistent } from './../services/app-persistent-utility.service';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { User } from '../models/user';
import { DataSharingService } from 'src/services/data-sharing.service';
import { AfterViewChecked } from '@angular/core';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements AfterViewChecked {
  current: Persistent;

  title = 'Fatima Group Web Portal';
  // active tabe breadcrumb url is subscribed 
  // accorodingly ticket tab will be selected on login 
  public breadcrumbUrl = '';
  farmerFlag;
  dealerFlag;
  ticketFlag;
  newsFlag;
  fertilizerCalculatorFlag;
  dropDown: boolean = false;
  currentUser: User;
  loginName: string; // added by Husnain

  dropDownData = [
    {
      id: 1,
      name: 'ABC'
    },
    {
      id: 2,
      name: 'DEF'
    },
    {
      id: 3,
      name: 'GHI'
    }
  ];
  farmer() {
    this.farmerFlag = 1; // farmer status
    this.dealerFlag = 0;
    this.breadcrumbUrl = 'Farmers';
  }

  dealers() {
    this.farmerFlag = 0;
    this.dealerFlag = 1; // dealer status 
    this.breadcrumbUrl = 'Dealers';
  }
  tickets() {
    this.farmerFlag = 0;
    this.dealerFlag = 0;
    this.ticketFlag = 1; // ticket status
    this.breadcrumbUrl = 'Tickets';
  }

  news() {
    this.farmerFlag = 0;
    this.dealerFlag = 0;
    this.ticketFlag = 0;
    this.newsFlag = 1;
    this.breadcrumbUrl = 'News';
  }

  fertilizerCalculator() {
    this.farmerFlag = 0;
    this.dealerFlag = 0;
    this.ticketFlag = 0;
    this.newsFlag = 0;
    this.fertilizerCalculatorFlag = 1;
    this.breadcrumbUrl = 'Fertilizer-Calculator';
  }

  openDropDown() {
    this.dropDown = !this.dropDown;
    console.log("Drop Down value: ", this.dropDown);
  }

  constructor(
    // private _appPersistenceUtility: AppPersistentUtilityService,
    private router: Router,
    private auth: AuthService,
    private dataSharingService: DataSharingService,
    private cdRef: ChangeDetectorRef) {
    this.auth.currentUser.subscribe(x => this.currentUser = x);
    console.log("Current user : ", this.currentUser);
    // this.loginName=null;
    this.loginName = localStorage.getItem('name');
    // this._appPersistenceUtility.ticketsPersistence.subscribe(res => {
    //   this.current = res;
    //   console.log(res);
    // });

  }

  ngOnInit() {
    //console.log(this.route);
  }

  ngAfterViewChecked() {
    this.dataSharingService.breadcrumbUrlStatus.subscribe((activeTab: string) => {
      if (activeTab == 'tickets') {
        this.tickets();
      }
      else if (activeTab == 'news') {
        this.news();
      }
      else if (activeTab == 'dealers') {
        this.dealers();
      }
      else if (activeTab == 'farmer') {
        this.farmer();
      }
      else if (activeTab == 'Fertilizer-Calculator') {
        this.fertilizerCalculator();
      }
    });

    this.loginName = localStorage.getItem('loginName');
    this.cdRef.detectChanges();
  }

  get isLoggedIn() {
    return this.auth.isLoggedIn;
  }


  logout(): void {
    console.log("Logout");
    this.auth.logout();
    this.router.navigate(['/login']);
  }


}
