import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';
import { __values } from 'tslib';

@Injectable({
  providedIn: 'root'
})
export class AppPersistentUtilityService {

  ticketsPersistence = new BehaviorSubject<Persistent>(new Persistent(0, 0, 0, '', 0, null, true));

  farmerPersistence = new BehaviorSubject<FarmerPersistent>(new FarmerPersistent(0, 0, 0, '', null, null, true));
  // dealerPersistence = new BehaviorSubject<Persistent>(new Persistent(2, 0, 0, null, false));
  // newsPersistence = new BehaviorSubject<Persistent>(new Persistent(3, 0, 0, null, false));
  // FertilizerCalculatorPersistence = new BehaviorSubject<Persistent>(new Persistent(4, 0, 0, null, false));

  constructor() { }
}


export class Persistent {
  appTab: number;
  currentPageNumber: number;
  tsoId: number;
  tsoName: string;
  totalPages: number;
  data: any
  isCurrent: boolean;

  constructor(appTab: number, currentPageNumber: number, tsoId: number, tsoName: string, totalPages: number, data: any, isCurrent: boolean) {
    this.appTab = appTab;
    this.currentPageNumber = currentPageNumber;
    this.tsoId = tsoId;
    this.tsoName = tsoName;
    this.totalPages = totalPages;
    this.data = data;
    this.isCurrent = isCurrent;
  }
}

enum TicketsTabs {
  All = 0,
  Open = 1,
  InProgress = 2,
  Closed = 3,
  OverDue = 4,
  OnHold = 5
}

export class FarmerPersistent {
  appTab: number;
  currentPageNumber: number;
  cat: string;
  val: any;
  totalPages: number;
  data: any
  isCurrent: boolean;

  constructor(appTab: number, currentPageNumber: number, totalPages: number, cat: string, val: any, data: any, isCurrent: boolean) {
    this.appTab = appTab;
    this.currentPageNumber = currentPageNumber;
    this.cat = cat;
    this.val = val;
    this.totalPages = totalPages;
    this.data = data;
    this.isCurrent = isCurrent;
  }
}

enum FarmerTabs {
  All = 0,
  Punjab = 1,
  Sindh = 2,
  Balochistan = 3,
  KPK = 4,
  GB = 5,
  AJK = 6
}