@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private currentUserSubject: BehaviorSubject<User>;
  public currentUser: Observable<User>;
  // private _loginURL = 'https://119.63.133.166:8181/api/user/authenticate';
  // private _loginURL = 'https://agriappapi.fatima-group.com:8181/api/user/authenticate';
  private _loginURL = 'http://10.100.37.30:8086/api/user/authenticate';

  // private _loginURL = 'http://10.100.91.110:8086/api/user/authenticate';
  // private _loginURL = 'http://10.100.91.110:8086/api/user/authenticate';
  isLoggedIn = false;
  // public token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1bmlxdWVfbmFtZSI6IjkyMzM1NTI0MjMzOSIsInJvbGUiOiJDYWxsIHNlcnZpY2UgYWdlbnQiLCJodHRwOi8vc2NoZW1hcy54bWxzb2FwLm9yZy93cy8yMDA1LzA1L2lkZW50aXR5L2NsYWltcy9hbm9ueW1vdXMiOiI2ZDJjNWZmNS0zZDEyLTQ1YmItYTNjMS1jZjhhNzQzMTkyMDYiLCJuYmYiOjE2MjM5MzQ1ODUsImV4cCI6MTYyNDAyMDk4NSwiaWF0IjoxNjIzOTM0NTg1fQ.WqHSrUdLIc2XcbgmI3xYA0DogVs_62uZQWQwWKF3TSI";
  // public token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1bmlxdWVfbmFtZSI6IjkyMzM1NTI0MjMzOSIsInJvbGUiOiJDYWxsIHNlcnZpY2UgYWdlbnQiLCJodHRwOi8vc2NoZW1hcy54bWxzb2FwLm9yZy93cy8yMDA1LzA1L2lkZW50aXR5L2NsYWltcy9hbm9ueW1vdXMiOiI2ZDJjNWZmNS0zZDEyLTQ1YmItYTNjMS1jZjhhNzQzMTkyMDYiLCJuYmYiOjE2MjQ0NTc0MDYsImV4cCI6MTYyNDU0MzgwNiwiaWF0IjoxNjI0NDU3NDA2fQ.EMvvfuTq1ZhbncgvNFkX0w7QXWb2ff3frUicDiQiIp8";

  constructor(private http: Http, private httpClient: HttpClient, private ts: ToastrService) {
    this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
    this.currentUser = this.currentUserSubject.asObservable();
  }

  public get currentUserValue(): User {
    return this.currentUserSubject.value;
  }

  authenticateLogin(login: User) {
    // localStorage.setItem('name', login.UserName);
    return this.httpClient.post<any>(`${this._loginURL}`, login, {})
      .pipe(map(user => {
        if (user && user.responseCode === 0) {
          localStorage.setItem('currentUser', JSON.stringify(user));
          localStorage.setItem('loginName', login.UserName);

          this.currentUserSubject.next(user);
          this.ts.success("Logged In Successfully", '', { timeOut: 1000 });
          this.isLoggedIn = true;
        }
        else {
          this.ts.error("Failed to Logged In", '', { timeOut: 1000 });
        }
        return user;
      })

      );

  }

  errorHandler(error: Response) {
    console.log(error);
    return Observable.throw(error);
  }

  logout(): void {
    localStorage.removeItem('currentUser');
    localStorage.removeItem('loginName');
    // localStorage.removeItem('name');
    this.currentUserSubject.next(null);
    this.isLoggedIn = false;
  }

  getAuthorizationToken() {
    return JSON.parse(localStorage.getItem('currentUser'));
  }


}