import { FertilizerCalculator } from './../fertilizerCalculator';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataSharingService } from 'src/services/data-sharing.service';
import { FertilizerCalculatorService } from '../fertilizer-calculator.service';
import { MockData } from 'src/constants/mock-data';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-list-fertilizer-calculator',
  templateUrl: './list-fertilizer-calculator.component.html',
  styleUrls: ['./list-fertilizer-calculator.component.css']
})
export class ListFertilizerCalculatorComponent implements OnInit {
  fertilizerCalculators: FertilizerCalculator[];
  private currentPage: number = 0;
  public recordsPerPage: number = 15;
  public totalrecords: number = 0;
  currentpage;

  private pages: number = 1;
  showBoundaryLinks = true;
  public runProcessCellCallback: boolean = true;

  private searchedcat: string;
  searchvalue;
  private searchedValue: string = "";

  breadcrumbUrl;

  public colDef: any[];
  public workFlowViewcolumnDefs: any[];
  public workFlowViewrowData: any[];

  private params: string;

  constructor(
    public fertilizerCalcutorService: FertilizerCalculatorService,
    private router: Router,
    private dataSharingService: DataSharingService) { }

  ngOnInit() {
    // set active tab status
    this.dataSharingService.activeBreadCrumburl('Fertilizer-Calculator');

    this.fertilizerCalcutorService.getAllFertilizerCalculation('pageNumber=' + this.currentPage + '&pageSize=' + this.recordsPerPage)
      .pipe(first()).subscribe(data => {
        this.fertilizerCalculators = data['data'];
        this.totalrecords = data['totalCount'];
        this.getWfGridData();
      });

  }

  pageChanged(event: any) {
    this.pages = event.page;
    this.pages = this.pages - 1;
    this.params = 'pageNumber=' + this.pages + '&pageSize=' + this.recordsPerPage;
    this.fertilizerCalcutorService.getAllFertilizerCalculation(this.params)
      .pipe(first()).subscribe(data => {
        this.fertilizerCalculators = data['data'];
        this.totalrecords = data['totalCount'];
        this.getWfGridData();
      });
  }

  getWfGridData() {
    this.colDef = MockData.agGridColumnDefs_FertilizerCalculator;
    this.workFlowViewcolumnDefs = this.colDef;
    this.workFlowViewrowData = this.fertilizerCalculators;
  }

  addedit(id) {
    this.router.navigate(['/add-fertilizer-calculator/' + id]);
  }

  onGridReady(params) {
    params.api.sizeColumnsToFit();
  }
}
