import { LocalStorageService } from './../../../services/local-storage.service';
import { FertilizerCalculatorService } from './../../fertilizer-calculator/fertilizer-calculator.service';
import { NewsService } from './../../news/news.service';
import { DealerService } from './../../dealer/dealer.service';
import { TicketService } from './../../ticket/ticket.service';
import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { GridOptions } from 'ag-grid';

import * as selectListEditorModule from './ag-grid-components/select-list.editor';
import * as selectListRendererModule from './ag-grid-components/select-list.renderer';
import * as agAdditioanlActionsRendererModule from './ag-grid-components/ag-additional-action.renderer';
import * as agActionsRendererModule from './ag-grid-components/ag-action.renderer';
import * as agStatusRendererModule from './ag-grid-components/ag-status.renderer';
import { EventEmitterService } from '../../../services/event-emitter.service';
import * as moment from 'moment';
import { DatePipe } from '@angular/common';
import { FarmersService } from 'src/components/farmer/farmers.service';
import { first } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';



@Component({
    selector: 'app-ag-grid-base',
    templateUrl: './ag-grid-base.component.html',
    styleUrls: ['./ag-grid-base.component.css']
})
export class AgGridBaseComponent implements OnInit, OnChanges {
    frameworkComponents: {};
    @Input() rowData: any;
    @Input() superTab: Number;
    @Input() columnDefs: any;
    @Input() gridOptions: any;
    @Input() gridParamters: any;
    @Input() headerHeight: any;
    @Input() agGridRowHeight: number;
    @Input() totalCount: number;
    @Input() pagination: boolean = false;
    @Input() paginationAutoPageSize: boolean = false;
    @Output() onRowSelected: any = new EventEmitter<any>();

    // added by Husnain
    // to check whether to run processCellCallback before exporting CSV
    @Input() runProcessCellCallback: boolean = false;

    public gridColumnApi;
    public overlayLoadingTemplate;
    public overlayNoRowsTemplate;

    components: {};
    public rowSelection;
    editType: string;

    private gridApi;
    public gridOptionsExcel: any;

    constructor(
        public ticketService: TicketService,
        public farmersService: FarmersService,
        public dealerService: DealerService,
        public newsService: NewsService,
        private ts: ToastrService,
        public FertilizerCalculatorService: FertilizerCalculatorService,
        public localStorage: LocalStorageService,
        private eventEmitterService: EventEmitterService,
        private datePipe: DatePipe) {
        this.gridOptionsExcel = {
            pagination: true,
            enableSorting: true
        }
        this.rowSelection = "multiple";
        this.overlayLoadingTemplate =
            '<span class="ag-overlay-loading-center">Please wait while data is loading...</span>';
        this.overlayNoRowsTemplate = '<span>No Records Found<span>';
    }

    ngOnInit() {
        //this.sizeToFit();
        this.editType = 'fullRow';
        this.frameworkComponents = {
            AgStatusActionsRenderer: agStatusRendererModule.AgStatusActionsRenderer,
            AgGridCellSelectEditor: selectListEditorModule.AgGridCellSelectEditor,
            AgGridCellSelectRenderer: selectListRendererModule.AgGridCellSelectRenderer,
            AgGridActionsRenderer: agActionsRendererModule.AgActionsRenderer,
            AgGridAdditionalActionsRenderer: agAdditioanlActionsRendererModule.AgAdditionalActionsRenderer
        };

        if (this.eventEmitterService.subsVar == undefined) {
            this.eventEmitterService.subsVar = this.eventEmitterService.
                removeRowEmitter.subscribe((name: string) => {
                    this.removeRow();
                });
        }
        this.gridOptions = <GridOptions>{
            context: {
                validateSheetContext: this
            },
            enableFilter: true,
            enableSorting: true,
            defaultColDef: {
                // editable: true
                // stopEditingWhenGridLosesFocus=true,
            },
            onRowEditingStopped: this.onRowUpdateComplete,
            pagination: true
        };
    }

    removeRow() {
        alert("remove called")
        var selectedData = this.gridApi.getSelectedRows();
        this.gridApi.updateRowData({ remove: selectedData });
    }


    convertToCsv() {
        var params = {
            fileName: 'Reports',
            allColumns: true,
            onlySelected: false, // only selected rows to be exported
        }

        // runProcessCellCallback for components that requires reformating before export
        if (this.runProcessCellCallback) {
            // A callback function invoked once per cell in the grid. Return a string value to be displayed in the export
            // used to reformat each col cell before being passed to export
            params['processCellCallback'] = this.processCellCallback;
        }
        // var csv: ExcelExportParams = <ExcelExportParams>{};
        // csv.allColumns = true;
        // csv.skipGroups = true;
        // csv.suppressQuotes = true;
        // csv.fileName = 'Report';
        // csv.onlySelected=true;
        //var BOM = "\uFEFF";

        if (this.gridApi.getSelectedRows().length != 0) {
            params = {
                fileName: 'Reports',
                allColumns: true,
                onlySelected: true, // only selected rows to be exported
            }
            this.gridOptionsExcel.api.exportDataAsCsv(params);
        }
        else {
            if (this.superTab == 0) {
                this.ts.show("Please wait. Downloading...");
                this.ticketService.getAllTickets()
                    .pipe(first()).subscribe(data => {
                        this.exportCsv(data["data"], params);
                    });
            }
            else if (this.superTab == 1) {
                this.ts.show("Please wait. Downloading...");
                this.farmersService.getAllFarmers()
                    .pipe(first()).subscribe(data => {
                        this.exportCsv(data["data"], params);
                    });
            }
            else if (this.superTab == 2) {
                this.ts.show("Please wait. Downloading...");
                this.dealerService.getAllDealers()
                    .pipe(first())
                    .subscribe(data => {
                        this.exportCsv(data, params);
                    });
            }
            else if (this.superTab == 3) {
                this.ts.show("Please wait. Downloading...");
                this.newsService.getAllNewsNoParams()
                    .pipe(first())
                    .subscribe(data => {
                        this.exportCsv(data, params);
                    });
            }
            else if (this.superTab == 4) {
                this.ts.show("Please wait. Downloading...");
                this.FertilizerCalculatorService.getAllFertilizerCalculations()
                    .pipe(first()).subscribe(data => {
                        this.exportCsv(data["data"], params);
                    });
            }
        }
    }

    exportCsv(data, params) {
        let excelImport = this.gridApi;
        excelImport.setRowData(data);
        excelImport.exportDataAsCsv(params);
        this.gridApi.setRowData(this.rowData);
    }

    ngOnChanges(changes: any) {
        const agGridRowHeight = changes['agGridRowHeight'];
        if (agGridRowHeight === undefined) {
            this.agGridRowHeight = 37;
        } else {
            this.gridOptions.rowHeight = agGridRowHeight;
        }
    }

    sizeToFit() {
        this.gridApi.sizeColumnsToFit();
    }

    onGridReady(params) {
        this.gridApi = params.api;
        params.api.showLoadingOverlay();
        //this.gridApi.sizeColumnsToFit();
        //this.gridApi.autoSizeAllColumns();
        this.gridColumnApi = params.columnApi;

        if (this.agGridRowHeight === undefined) {
            this.agGridRowHeight = 37;
        } else {
            this.gridOptions.rowHeight = this.agGridRowHeight;
        }
        setTimeout(function () {
            params.api.resetRowHeights();
        }, 500);
    }


    onSelectionChanged(event) {
        if (this.gridApi.getSelectedRows().length === 0) {
            return;
        }
        this.onRowSelected.emit(this.gridApi.getSelectedRows());
    }

    selectEditorValueChange($event) {
        console.log('value rec: ' + $event);
    }

    onRowUpdateComplete(event) {
        console.log(event);
    }


    // added by Husnain
    processCellCallback(params) {
        console.log("params: ", params.column.getColId());
        console.log("value: ", params.value);
        // reformat to remove un-necessary HTML tags
        if (params.column.getColId() === 'body') {
            let temporalDivElement = document.createElement('div');
            // Set the HTML content with the providen
            temporalDivElement.innerHTML = params.value;
            // Retrieve the text property of the element (cross-browser support)
            params.value = temporalDivElement.textContent || temporalDivElement.innerText || '';
        }
        // data format
        else if (params.column.getColId() == 'modifiedDateTime' || params.column.getColId() == 'insertionDate'
            || params.column.getColId() == 'data.createdDate' || params.column.getColId() == 'data.resolvedDate') {
            params.value = params.value ? moment(params.value).format('DD.MM.YYYY hh:mm') : '';
        }
        // data format for birth day
        else if (params.column.getColId() == 'dateOfBirth') {
            params.value = params.value ? moment(params.value).format('DD.MM.YYYY') : '';
        }
        return params.value;
    }

    // private setupDataGrid(): void {
    //     this.gridOptions = DataGrid.GetDefaults(this, 'LINEITEMS', this.localStorage);

    //     this.gridOptions.getRowNodeId = (data: any) => {
    //         return data.LineItemId;
    //     };

    //     this.gridOptions.columnDefs = <ColDef[]>[{ col defs go here }];

    //     // now just just use this.gridOptions like you normally would in your component
    // }
}

function getBooleanValue(cssSelector) {
    return document.querySelector(cssSelector).checked === true;
}