import { CustomValidator } from '../../../Validators/customValidator';
import { CropSowingInfo } from './../sowingInfo';
import { FertilizerCalculator } from './../fertilizerCalculator';
import { Component, OnInit } from '@angular/core';
import { DatePipe, Location } from '@angular/common';
import { FertilizerCalculatorService } from '../fertilizer-calculator.service';
import { first } from 'rxjs/operators';
import { Crops } from 'src/components/farmer/crops';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-add-edit-fertilizer-calculator',
  templateUrl: './add-edit-fertilizer-calculator.component.html',
  styleUrls: ['./add-edit-fertilizer-calculator.component.css']
})
export class AddEditFertilizerCalculatorComponent implements OnInit {

  myDate = new Date();
  crops: Crops[];
  sowingTimes: CropSowingInfo[];

  form: FormGroup;
  selectedCrop: string = 'Select Crop';
  selectedSowingTime: string = 'Select Sowing Time';

  public readOnly = false;
  id: number;
  isNew: boolean = false;
  FertilizerCalculator: FertilizerCalculator = new FertilizerCalculator();

  getsowingTime() {
    return this.form.controls.sowingTime.value;
  }


  constructor(
    private datePipe: DatePipe, private router: Router,
    private route: ActivatedRoute,
    fb: FormBuilder,
    private location: Location,
    private ts: ToastrService,
    public fertilizerCalcutorService: FertilizerCalculatorService) {

    this.form = fb.group({
      "crops": ["", [Validators.required, Validators.min(1)]],
      "sowingTime": ["", [Validators.required, Validators.min(1)]],

      "N": [Number, [Validators.required, Validators.max(999999999), CustomValidator.whleNumberValidator]],
      "P205": [Number, [Validators.required, Validators.max(999999999), CustomValidator.whleNumberValidator]],
      "K20": [Number, [Validators.required, Validators.max(999999999), CustomValidator.whleNumberValidator]],
      // cmbination 1
      "NP1": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "CAN1": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "SOP1": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      // cmbinatioNumber2
      "DAP2": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "Urea2": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "SOP2": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      // cmbinatioNumber3
      "NP3": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "Urea3": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "SOP3": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      // cmbinatioNumber4
      "DAP4": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "CAN4": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]],
      "SOP4": [Number, [Validators.required, Validators.max(99999), CustomValidator.numberValidator]]
    });
  }

  ngOnInit() {
    this.fertilizerCalcutorService.getCrops().pipe(first()).subscribe(cropdata => {
      this.crops = cropdata;
    });
    // Important
    // this.form.get('crops').valueChanges.subscribe(val => {
    //   console.log("******************crops******************************", val)
    // });
    // this.form.get('sowingTime').valueChanges.subscribe(val => {
    //   console.log("******************sowingTime******************************", val)
    // });
    this.getFertilizerCalcutor();
  }

  getFertilizerCalcutor(): void {

    this.id = +this.route.snapshot.paramMap.get('id');

    this.route.queryParams
      .filter(params => params.view)
      .subscribe(params => {
        this.readOnly = params.view;
        (this.readOnly) ? this.ReadOnly() : null;
      });

    this.id ? this.fertilizerCalcutorService.getfertilizerCalculation(this.id)
      .subscribe(data => {
        this.FertilizerCalculator = data;
        this.selectedCrop = this.FertilizerCalculator.tbCropTypeCodes.cropName;
        // this.onCropSelection();
        var cropID = this.crops.find(x => x.cropName == this.selectedCrop).cropCode;
        this.fertilizerCalcutorService.getCropsSowingInfo(cropID).pipe(first()).subscribe(data => {
          this.sowingTimes = data;
          this.selectedSowingTime = this.sowingTimes[0].sowingDate;
        });
        this.selectedSowingTime = this.FertilizerCalculator.tbCropSowingDates.sowingDate;
      })
      : this.isNew = true;
  }

  get f() { return this.form.controls; }

  goBack() {
    this.location.back();
  }

  delete(): void {
    if (this.id != 0) {
      this.fertilizerCalcutorService.deletefertilizerCalculation(this.FertilizerCalculator)
        .subscribe(data => {
          if (data != undefined) {
            this.ts.success("Delete successfully.");
            this.router.navigate(['fertilizer-calculator/']);
          }
        });
    }
  }

  onCropSelection() {
    var cropID = this.crops.find(x => x.cropName == this.selectedCrop).cropCode;
    this.fertilizerCalcutorService.getCropsSowingInfo(cropID).pipe(first()).subscribe(data => {
      this.sowingTimes = data;
      this.selectedSowingTime = this.sowingTimes[0].sowingDate;
    });
  }

  save() {
    if (this.selectedCrop != 'Select Crop') {
      this.FertilizerCalculator.cropCode = this.crops.find(x => x.cropName == this.selectedCrop).cropCode;
    }
    if (this.selectedSowingTime != 'Select Sowing Time') {
      this.FertilizerCalculator.cropCaseCode = this.sowingTimes.find(x => x.sowingDate == this.selectedSowingTime).sowingDateId;
    }
    if (this.form.valid
      && this.selectedCrop != 'Select Crop'
      && this.selectedSowingTime != 'Select Sowing Time') {
      if (this.FertilizerCalculator.fertilizerCalID == 0 || this.FertilizerCalculator.fertilizerCalID == undefined) {
        this.fertilizerCalcutorService.addFertilizerCalculation(this.FertilizerCalculator)
          .pipe(first())
          .subscribe(data => {
            if (data != undefined) {
              this.ts.success("New record is added successfully.");
              this.goBack();
            }
          });
      }
      else {
        this.fertilizerCalcutorService.updatefertilizerCalculation(this.FertilizerCalculator)
          .pipe(first())
          .subscribe(data => {
            if (data != undefined) {
              this.ts.success("Record is updated successfully.");
              this.goBack();
            }
          });
      }
    }
    else {
      this.ts.info("Must fill all fields properly.");
    }
  }

  ReadOnly() {
    this.form.controls['sowingTime'].disable();
    this.form.controls['crops'].disable();
    this.form.controls['N'].disable();
    this.form.controls['P205'].disable();
    this.form.controls['K20'].disable();
    this.form.controls['NP1'].disable();
    this.form.controls['CAN1'].disable();
    this.form.controls['SOP1'].disable();
    this.form.controls['DAP2'].disable();
    this.form.controls['Urea2'].disable();
    this.form.controls['SOP2'].disable();
    this.form.controls['NP3'].disable();
    this.form.controls['Urea3'].disable();
    this.form.controls['SOP3'].disable();
    this.form.controls['DAP4'].disable();
    this.form.controls['CAN4'].disable();
    this.form.controls['SOP4'].disable();
  }


}
