import { Crops } from './../farmer/crops';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { AppConfig } from 'src/constants/app-config';
import { FertilizerCalculator } from './fertilizerCalculator';
import { CropSowingInfo } from './sowingInfo';

@Injectable({
  providedIn: 'root'
})
export class FertilizerCalculatorService {

  constructor(private http: HttpClient, private ts: ToastrService) { }


  //DONE
  getAllFertilizerCalculation(params: string) {
    return this.http.get<FertilizerCalculator[]>(`${AppConfig.URL_fertilizerCalculation}AllFertilizerCalculator?` + params)
      .pipe(
        map(x => {
          return x["data"];
        }),
        tap(_ => console.log('fetched FertilizerCalculator')),
        catchError(this.handleError('Could not get records, try later.'))
      );
  }

  //DONE
  getAllFertilizerCalculations() {
    return this.http.get<FertilizerCalculator[]>(`${AppConfig.URL_fertilizerCalculation}GetAllFertilizerCalculatorInfo`)
      .pipe(
        map(x => {
          return x["data"];
        }),
        tap(_ => console.log('fetched FertilizerCalculator')),
        catchError(this.handleError('Could not get records, try later.'))
      );
  }

  //DONE
  addFertilizerCalculation(fertilizerCalculation: FertilizerCalculator): Observable<FertilizerCalculator> {
    return this.http.post<FertilizerCalculator>(`${AppConfig.URL_fertilizerCalculation}FetilizerCalculator`, fertilizerCalculation)
      .pipe(
        map(x => {
          return x["data"];
        }),
        tap(
          (fertilizerCalculation: FertilizerCalculator) =>
            console.log(`added FertilizerCalculator w/ id=${fertilizerCalculation.fertilizerCalID}`)
        ),
        catchError(
          this.handleError<FertilizerCalculator>('Record already exist.')
        )
      );
  }

  updatefertilizerCalculation(fertilizerCalculation: FertilizerCalculator): Observable<FertilizerCalculator> {
    return this.http.put(`${AppConfig.URL_fertilizerCalculation}FetilizerCalculator/${fertilizerCalculation["fertilizerCalID"]}`, fertilizerCalculation)
      .pipe(
        tap(_ => console.log(`updated fertilizerCalculation id=${fertilizerCalculation["fertilizerCalID"]}`)),
        catchError(this.handleError<any>('Could not update, try later.'))
      );
  }

  //
  getfertilizerCalculation(id: number) {
    return this.http.get<FertilizerCalculator>(`${AppConfig.URL_fertilizerCalculation}FertilizerCalculatorbyId/${id}`)
      .pipe(
        map(x => {
          return x["data"]
        }),
        tap(_ => console.log(`Get fertilizerCalculation id=${id}`)),
        catchError(this.handleError<any>('Could not get data, try later.'))
      );
  }

  deletefertilizerCalculation(fertilizerCalculation: FertilizerCalculator | number): Observable<FertilizerCalculator> {
    const id = typeof fertilizerCalculation === 'number' ? fertilizerCalculation : fertilizerCalculation.fertilizerCalID;
    const url = `${AppConfig.URL_fertilizerCalculation}DeleteFertilizerCalculator/${id}`;
    return this.http.delete<FertilizerCalculator>(url)
      .pipe(
        map(x => {
          return x["data"];
        }),
        tap(_ => console.log(`deleted FetilizerCalculator id=${id}`)),
        catchError(this.handleError<FertilizerCalculator>('Could not delete, try later.'))
      );
  }

  getCrops() {
    return this.http.get<Crops>(`${AppConfig.URL_Crops}`).pipe(
      map(x => {
        return x["data"]
      }),
      tap(_ => console.log(`getCrops`)),
      catchError(this.handleError<any>('Could not get crops, try later.'))
    );
  }

  getCropsSowingInfo(id: number) {
    return this.http.get<CropSowingInfo>(`${AppConfig.URL_Crops}SowingInfoByCrop/${id}`).pipe(
      map(x => {
        return x["data"];
      }),
      tap(_ => console.log(`getCropSowingInfo`)),
      catchError(this.handleError<any>('Could not get Crops Swoing dates, try later.'))
    );
  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.ts.error(`${operation}`);
      // this.ts.error("Failed to Perform Operation; " + operation);
      return of(result as T);
    };
  }

  private log(message: string) {
    this.ts.success(message);
  }
}

